$primaryColor: #4527a4;
$secondaryColor: #8344c5;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Accordion
.wp-block-bab-accordion {
	* {
		box-sizing: border-box;
	}

	.babAccordion {
		dt {
			padding: 10px 8px;
			margin: 0;
			cursor: pointer;
		}

		dd {
			padding: 10px;
			margin: 0;
			overflow: hidden;
		}

		&.horizontal {
			display: flex;
			height: 200px;

			dt {
				width: 90px;
				height: 100%;
			}

			dd {
				width: 0;
				height: 100%;
				display: none;
			}

			@media (max-width: 576px) {
				flex-direction: column;
				height: auto;

				dt {
					width: 100%;
					height: auto;
				}

				dd {
					width: 100%;
					height: auto;
				}
			}
		}
	}
}